import React, { useState } from "react"
import styled from 'styled-components';
import FindColorTwo from './findColorTwo';
import SortDivHome from './sortDivHome';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowLeft } from '@fortawesome/free-solid-svg-icons';
import WindowDimensionsProvider from "./widgets/WindowsDimensionProvider";
import { Button } from "./findColor";
import Iphone from "./pages/iphone";

export default function Home() {

  const [initialSet, setInitialSet] = useState(true);
  const [color, setColor] = useState(false);
  const [layout, setLayout] = useState(false);
  const [hideme, setHideme] = useState(false);

  const colorClick = e => {
    e.preventDefault();
    setColor(true);
    setLayout(false);
    setInitialSet(false);
    setHideme(true);
  }

  const layoutClick = e => {
    e.preventDefault(e);
    setColor(false);
    setLayout(true);
    setInitialSet(false);
    setHideme(true);
  }

  const start = e => {
    e.preventDefault();
    setColor(false);
    setLayout(false);
    setInitialSet(true);
    setHideme(false);
  }

  return (<><WindowDimensionsProvider>
    <Wrapper>
      {hideme && (<>
        <BtnWrapper>
          <Button onClick={() => setHideme(false)}>App on Iphone</Button>
        </BtnWrapper>
        <FindColorTwo />
      </>)}
      {!hideme && (<>
        <BtnWrapper>
          <Button onClick={() => setHideme(true)}>Go Back</Button>
        </BtnWrapper>
        <Iphone />
      </>)}
      {/* {!hideme &&
        <Header>Choose A Skill</Header>
      }
      {initialSet &&
        <>
          <Choices>
            <One onClick={colorClick}>Color Codes</One>
            <Two onClick={layoutClick}>Layouts</Two>
          </Choices>
        </>
      }
      {color && <>
        <div><Button onClick={start}><FontAwesomeIcon icon={faArrowLeft} />Go Back</Button></div>
        <FindColorTwo />
      </>
      }
      {layout && <>
        <div><Button onClick={start}><FontAwesomeIcon icon={faArrowLeft} />Go Back</Button></div>
        <SortDivHome />
      </>
      } */}
    </Wrapper>
  </WindowDimensionsProvider></>)
}

const BtnWrapper = styled.div`
  width: 100%;
  justify-content: center;
  display: flex;
`;

// const Button = styled.div`
//   font-size: 20px;
//   font-weight: 500;
//   margin-bottom: 30px;
//   cursor: pointer;
//   & svg {
//     padding-right: 10px;
//   }
// `;
export const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  width: min(1200px, 100%);
  margin: 50px auto 30px;
`;

const Header = styled.div`
  font-size: 30px;
  line-height: 36px;
`;


const Choices = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-top: 20px;
  grid-gap: 20px;
  background-color: white;
`;

const Boxes = styled.div`
  border: 1px solid rgba(128,128,128,0.3);
  padding: 20px;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s ease;

`;

const One = styled(Boxes)`
  &:hover {
    background-color: blue;
    color: white;
  }
`;
const Two = styled(Boxes)`
  &:hover {
    background-color: orange;
    color: white;
  }
`;
